import { Injectable } from '@angular/core';
import { AttachmentConfig } from '../urls/rbac-url.config';
import { DataStoreService } from './data-store.service';

@Injectable({
  providedIn: 'root'
})
export class AttachmentsService {
  http:any;
  constructor(private _storeservice: DataStoreService) {
    this._storeservice.currentStore.subscribe((res: any) => {
      if(res){
        this.http = res['HTTPSERVICE']
      }
    })
  }  uploadKey(objparams: any) {
    return this.http.post(AttachmentConfig.EndPoint.Attachments.UploadKey, objparams);
  }
}
