import { Component, Injector, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ConfirmationService } from 'primeng/api';
import { Subscription } from 'rxjs';
import { ProfileService } from '../../profile.service';
import { AppConstants } from '../@core/constants/app-constants';
import { AlertService } from '../@core/service/alert.service';
import { ThemeService } from '../@core/service/theme.service';
import { CustomValidator } from '../@core/validator/custom-validator';
import { fontSetList, themeList } from '../@core/constants/theme-constants';
import { AttachmentsService } from '../@core/service/attachments.service';
import { LocalService } from '../@core/service/local.service';
import { AuthService } from '../@core/service/auth.service';
import { RBACINFO } from '../@core/urls/rbac-url.config';
import { DataStoreService } from '../@core/service/data-store.service';
import { DatePipe } from '@angular/common';
declare const $ : any;

@Component({
  selector: 'core-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.scss']
})
export class ProfileComponent implements OnInit {
  userForm!: FormGroup;
  userid: any;
  thumbnail!: string;
  imageData: any;
  uploadedFile: any;
  urlPath: any;
  attachType: any;
  resetPasswordForm!: FormGroup;
  alertService: AlertService;
  localstorage: LocalService;
  isValid = false;
  socketSubscription!: Subscription;
  currentDate = new Date();
  themes: any = [];
  fontSetList: any = [];
  selectedTheme: any;
  selectedFont: any;
  themeService: ThemeService;
  rocketIcon!: string;
  environment: any;
  RBACORG: RBACINFO = new RBACINFO();
  orgSubs!: Subscription;
  orgId: any;
  errors = AppConstants.errorList;
  httpService:any;

  constructor(
    injector: Injector,
    private authService: AuthService,
    private formBuilder: FormBuilder,
    private profileService: ProfileService,
    private attachmentService: AttachmentsService,
    private _storeservice: DataStoreService,
    private confirmationService: ConfirmationService
  ) {
    this.localstorage = injector.get<LocalService>(LocalService);
    this.alertService = injector.get<AlertService>(AlertService);
    this.themeService = injector.get<ThemeService>(ThemeService);
    this.themes = themeList;
    this.fontSetList = fontSetList;
    this.userid = sessionStorage.getItem('id');
    this.selectedTheme = this.localstorage.getItem('SELECTED_THEME') || 'default';
    this.selectedFont = this.localstorage.getItem('SELECTED_FONT') || '13';
    this.initializeResetPasswordForm();
    this.initializeForm();
  }

  ngOnInit(): void {
    this.orgSubs =  this._storeservice.currentStore.subscribe((res: any) => {
      if (res['RBACORG'] && res['RBACORG'] !== '') {
        this.RBACORG = res['RBACORG'];
        console.log(this.RBACORG, 'RBACORG Profile');
        this.environment = this.RBACORG['environment'];
        this.orgId = parseInt(this.RBACORG['orgID']);
        this.httpService = res['HTTPSERVICE']
        if(this.environment){
          this.getUserInfo();
          this.fontSlider();
        }
      }
    });
  }
  initializeResetPasswordForm() {
    this.resetPasswordForm = this.formBuilder.group({
      id: sessionStorage.getItem('id'),
      mailid: sessionStorage.getItem('email'),
      currentpassword: ['', Validators.required],
      newpassword: [
        '',
        Validators.compose([
          Validators.required,
          CustomValidator.patternValidator(/\d/, { hasNumber: true }),
          CustomValidator.patternValidator(/[A-Z]/, { hasCapitalCase: true }),
          CustomValidator.patternValidator(/[a-z]/, { hasSmallCase: true }),
          CustomValidator.patternValidator(/[ !@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?]/, { hasSpecialCharacters: true }),
          Validators.minLength(8)
        ])
      ],
      conformpassword: ['', Validators.required]
    });
  }

  initializeForm() {
    this.userForm = this.formBuilder.group({
      firstname: ['', Validators.required],
      middlename: [''],
      lastname: ['', Validators.required],
      username: ['', Validators.required],
      // dob: [''],
      email: ['', [Validators.required, Validators.pattern(AppConstants.regexEmail)]],
    });
  }

  get resetFormcontrols() {
    const isInvalid = this.resetPasswordForm.controls['newpassword'].status;
    const newpassword = this.resetPasswordForm.value['newpassword'];
    const conformpassword = this.resetPasswordForm.value['conformpassword'];
    if (isInvalid === 'INVALID' && newpassword !== '') {
      this.isValid = true;
    } else {
      this.isValid = false;
    }
    if (newpassword !== conformpassword) {
      this.resetPasswordForm.get('conformpassword')!.setErrors({ NoPassswordMatch: true });
    }
    return this.resetPasswordForm.controls;
  }

  getUserInfo() {
    this.profileService.getAllUserList(this.userid).subscribe((res: any) => {
      const patchValuedata = res.data;
      this.thumbnail = patchValuedata.additionalinfo?.thumbnail?.url || '';
      this.userForm.patchValue({
        firstname: patchValuedata.firstname,
        middlename: patchValuedata.middlename,
        lastname: patchValuedata.lastname,
        username: patchValuedata.username,
        // dob: new Date(patchValuedata.dob),
        email: patchValuedata.email
      });
    });
  }

  handleFileInput(fileValue: any) {
    const target = fileValue.target as HTMLInputElement;
    const file: File = target.files![0];
    this.uploadedFile = fileValue.target.files[0].name;
    this.imageData = {
      contentType: fileValue.target.files[0].type,
      fileName: `user-profileimage/${this.userid}/${this.uploadedFile}`
    };
    if (this.validateImage(fileValue.target.files)) {
      this.attachmentService.uploadKey(this.imageData).subscribe(
        (res: any) => {
          this.urlPath = res.data;
          const uploadAttachment: any = document.getElementById('file');
          const uploadAttachmentDetails = uploadAttachment.files[0];
          this.httpService.putUpload(this.urlPath, uploadAttachmentDetails, uploadAttachmentDetails.type).subscribe(
            (_resp: any) => {
              this.alertService.success('Uploaded Successfully!');
              const reader = new FileReader();
              reader.onload = () => {
                this.thumbnail = reader.result as string;
              };
              reader.readAsDataURL(file);
              this.attachType = fileValue.target.files[0].type;
            },
            error => {
              if (error.status == 0) {
                this.alertService.error(AppConstants.errorMessage);
                this.uploadedFile = '';
                this.urlPath = '';
              }
            }
          );
        },
        _error => {
          this.alertService.error(AppConstants.errorMessage);
        }
      );
    }
  }

  updateprofile() {
    const userData = this.userForm.getRawValue();
    // userData.dob = new DatePipe('en-US').transform(userData.dob, 'YYYY-MM-dd');
    if (this.uploadedFile) {
      userData.additionalinfo = {
        photo: this.imageData,
        thumbnail: this.imageData
      };
    }
    this.profileService.updateUser(userData, this.userid).subscribe(() => {
      this.profileService.getProfile({
        url: this.thumbnail,
        name: this.userForm.value.firstname
      });
      this.alertService.success('Profile updated successfully');
    });
  }
  updateStyling() {
    const body = {
      type: 'USER_THEME_PREFERENCES',
      mappedid: this.userid,
      config: JSON.stringify({ theme: this.selectedTheme, font: this.selectedFont })
    };
    this.profileService.saveUserPreference(body).subscribe(() => {
      // This is intentional
    });
  }

  setTheme(event: any) {
    this.profileService.setTheme(event);
  }
  setFont(event: any) {
    this.profileService.setFont(event);
  }
  setRangeFont(modal: any){
    this.profileService.setRangeFont(modal);
  }
  changePassword() {
    const obj = {
      id: Number(this.resetPasswordForm.value['id']),
      currentpassword: this.resetPasswordForm.value['currentpassword'],
      newpassword: this.resetPasswordForm.value['newpassword']
    };
    this.authService.ResetPassword(obj).subscribe(
      _res => {
        this.authService.logout();
        this.alertService.success('Password Changed Successfully');
      },
      _error => this.alertService.error('Something went wrong')
    );
  }
  validateImage(file: any) {
    const fileSize = Number(file[0].size) / 1024;
    const filetype = file[0].type.replace('image/', '');
    if (filetype === 'jpg' || filetype === 'jpeg' || filetype === 'png' || filetype === 'gif') {
      if (fileSize < 50) {
        return true;
      } else {
        this.alertService.warn('File is bigger than 50 KB');
        return false;
      }
    } else {
      this.alertService.warn(`${filetype} format is not supported`);
      return false;
    }
  }

  clearForm() {
    this.userForm.controls['firstname'].reset();
    this.userForm.controls['middlename'].reset();
    this.userForm.controls['lastname'].reset();
    this.thumbnail = '';
  }

  onClickRemove(){
    $('#Deleteuser').modal('show');
  }

  cancel() {
    // this.userForm.reset();
    this.userForm.enable();

  }

  removeThumbnail() {
    // this.confirmationService.confirm({
    //   target: event.target as EventTarget,
    //   message: 'Are you sure you want to proceed?',
    //   icon: 'pi pi-exclamation-triangle',
    //   accept: () => {
      this.cancel();
        this.thumbnail = '';
        this.alertService.success('Profile image removed successfully.');
        $('#Deleteuser').modal('hide');
    //   },
    //   reject: () => {
    //       // reject message
    //   }
    // });
  }

  ngOnDestroy(): void {
    if (this.socketSubscription) {
      this.socketSubscription.unsubscribe();
    }
    this.orgSubs.unsubscribe();
  }

  fontSlider() {
    const rangeSlider: HTMLInputElement = document.getElementById('rs-range-line') as HTMLInputElement;
    const rangeBullet: HTMLElement = document.getElementById('rs-bullet') as HTMLElement;

    rangeSlider.addEventListener('input', showSliderValue, false);

    function showSliderValue(): void {
      rangeBullet.innerHTML = rangeSlider.value;
      const bulletPosition: number = (Number(rangeSlider.value) / Number(rangeSlider.max));
      rangeBullet.style.left = (bulletPosition * 578) + 'px';
    }
  }

}
